.swiper {
  width: 100%;
  height: 100%;

  
}
.slider-imges{
    max-width: 100%;
}
.mySwiper{
    margin-top: -1px !important;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  

 
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}


.swiper-slide img {
  display: block;
  width: 100%; 
  height: 450px ;   

 margin-top: 72px;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
}





@media screen and (max-width:1120px) {
  .swiper-slide img{
  margin-top:58px
  }
 }


 @media screen and (max-width:655px) {
  .swiper-slide img{
    height:290px;
    margin-top:45px
    }
 }




 