.sw-container{
  width: 200px;
  height: 300px;
  margin-left: -95px;

}
.center{
margin-left: 90px;
}
.swiper> img{
  height: 100px;
}
.swiper-slide {
  text-align: center;
  font-size: 12px;
  background: #fff;

  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-wrap: wrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}


#swiper-slide img {
  display: block;

  width: 90%;
  object-fit: cover;
}





