  #app { height: 100% }
html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}
.swiper {
  width: 100%;
  height: 100%;
  
}
.slider-imges{
    max-width: 100%;
}
.mySwiper{
    margin-top: 2px !important;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
 
  

  
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
 
}

 
@media screen and (max-width:515px) {
  .swiper-slide img {
    width: 100%; 
  }
  .swiper{
margin-top:-190px
  }
  .hom-swip{
    margin-top:200px
  } 
  
  }


.swiper-slide img {
  display: block;
  width: 100%; 
  height: 450px ;   

 
}

.swiper {
  margin-left: auto;
  margin-right: auto;
}


@media screen and (max-width:500px) {
 .shopebycategory{
  margin-top: 100px;
  margin-bottom: 200px;

  position: relative;
  top: -200px;
}
 }
 






