.h-pic-container{
    width: 100%;
    height:60%;
  
   display: flex;
   justify-content: center;
   flex-wrap: wrap;
   gap:10px
  }
  .h-pic1{
    transition-property: width ,height;
    width: 300px;
    transition-duration: 2s;
    height: 360px;
  }
  .h-pic1:hover{
  width: 280px;
  height: 340px;
  }
  .h-pic2{
    width: 500px;
    height: 360px;
    transition-property: width ,height;
    transition-duration: 2s;
  
  }
  .h-pic2:hover{
    width: 480px;
    height: 340px;
  }
  
  .h-pic3{
    transition-property: width ,height;
    transition-duration: 2s;
  
  width: 350px;
  height: 360px;
  }
  .h-pic3:hover{
    width: 330px;
  height: 340px;
  }
  .h-pic5{
    width: 300px;
    height: 300px;
    transition-property: width ,height;
    transition-duration: 2s;
   
   
  }
  .h-pic5:hover{
    width: 260px;
    height: 270px;
  }
  .h-pic4{
    width: 500px;
    height: 300px;
    transition-property: width ,height;
    transition-duration: 2s;
   
  }
  .h-pic4:hover{
    width: 480px;
    height: 260px;
  }
  .h-box1{
    width: 300px;
    height:360px;
  }
  .h-box2{
    width: 500px;
    height: 360px;
  }
  .h-box3{
    
  width: 350px;
  height: 360px;
  }
  .h-box4{
    width: 500px;
    height: 250px;
  }
  .h-box5{
    width: 300px;
    height: 300px;
  }

  

  @media screen and (max-width:1153px) {
   .h-pic4{
height:360px;
width: 370px;
   }
   .h-pic2{
    width:380px
   }
   
   }


   @media screen and (max-width:500px) {
    .h-pic5{
        margin-top:140px;
       display: none;

    }
    
    }

    
  
   
  