
  .newProduct {
    flex: 4;
  }
  
  .addProductForm {
    margin-top: 10px;
 
  }
  
  
  .addProductItem {
    width: 250px;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
   margin: auto;
  }
  
  .addProductItem > label {
    color: gray;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .addProductItem > input {
    padding: 10px;
  }
  .addProductTitle{
    text-align: center;
  }
  
  .addProductItem > select {
    padding: 10px;
  }
  
  .addProductButton {
    margin-top: 25px;
    padding: 7px 10px;
    border: none;
    border-radius: 10px;
    background-color: darkblue;
    color: white;
    font-weight: 600;
    cursor: pointer;
   position: relative;
   left: 411px;
   width: 256px;
   height: 50px;

  }


  @media screen and (max-width: 768px)
  {
    .addProductForm {
      margin-top: 10px;
   margin-left: 82px;
    }
    .addProductTitle{
      margin-left: 59px;
    }
    .addProductButton {
     
     position: relative;
     left: 0px;
     
  
    }
  }