.userpannelContainer{
    position: relative;
    /* top:141px; */
    
        width:fit-content;
        height: fit-content;
        /* border: 5px solid  rgb(40, 105, 131); */
        margin: auto;
    }
    .delete{
      height: 40px;
      border-radius: 30px;
         width: 100px;
         background-color:red;
         border: none;
         color: white;
         font-weight: bold;
          }
    .shoppingbutton{
      height: 40px;
      border-radius: 30px;
         width: 130px;
         border: none;
         background-color: rgb(12, 111, 12);
         color: white;
         font-weight: bold;
          }
          .link{
            text-decoration: none;
            color:#dddddd;
            font-weight: bold;
          }

    table {
        font-family: arial, sans-serif;
        border-collapse: collapse;
        width: 100%;
      
      } 
      
      td {
        border: 1px solid #dddddd;
        text-align: left;
        padding: 8px;
      }
    .th{
        background-color: black;
         color: white;
         font-weight: bold;
      } 




      @media screen and (max-width:500px) {
        th{
         display: none;
        }
        table{
          border: none;
        }
        table tr,table td{
         display: block;
         width: 90%;
        }
        table tr{
         margin-bottom: 15px;

        }
        .shoppingbutton{
          width: 130px;
        }

        table tr td{
         text-align: right;
         padding-left:50%;
         position: relative;
       
        }
       
        table tr td:before{
         content: attr( data-label);
         position: absolute;
         left: 0;
         width: 50%;
         padding-left: 15px;
         font-weight: bold;
         font-size: 17px;
         text-align: left;
        }
       
  
        }
